import React, {Component} from 'react';
import { Route , withRouter} from 'react-router-dom';
import {
    Button,
    Label,
    Table,
    Col, 
    Row 
  } from 'reactstrap';
//import AddLandlordModal from './AddLandlordModal'
// import EditModal from './EditModal'
import jwt_decode from 'jwt-decode';
import Spinner from '../Loaders/Spinner'
import '../Geographics/LocationStylee.css'
import Server_connections from '../../constants/constants';
import UserInfo from '../../constants/credentials'

class BookingList extends Component {
    constructor(props) {
        super(props);
        this.state = {
          token: '',
          landlord_code: '',
          bookings: [],
          isLoaded: false,
          searchItem: '',
          editModalShow: false
        }
        this.replaceModalItem = this.replaceModalItem.bind(this);
        this.handleRefreshButton = this.handleRefreshButton.bind(this);
    }

    replaceModalItem(index) {
      this.setState({
        requiredItem: index
      });
    }

    handleSearchInput = (e) => {
      this.setState({ searchItem: e.target.value})
    }

    handleRefreshButton(e){
      e.preventDefault();
      //this.props.history.push('/geographics')
      window.location.reload(false);
    }

    componentDidMount(){
      fetch(Server_connections.ALL_BOOKINGS_URL + `${UserInfo.UserCode}`)
      .then(res => res.json())
      .then(json => {
        this.setState({
          bookings:  json.bookings,
          isLoaded: true
        })
      })
    }

    render(){
      console.log(this.state.bookings);
      var { isLoaded, bookings } = this.state;

      let filteredItem = bookings.filter((bookings) => {
        return bookings.slot_ref.toLowerCase().includes(this.state.searchItem.toLowerCase());
      });

      let editModalClose = () => this.setState({editModalShow: false});
      let addModalClose = () => this.setState({addModalShow : false});

      const {productBarcode, productName, manufacturedDate, expiryDate, companyName, city, state, province, 
        location, postalAddress, plotNumber, contact, email, website} = this.state;

      if(!isLoaded){
        return <div className='spinner'>
          <Spinner/>
          </div>
      }
      else{
        return(
          <div>
              <div>
                  {/* <Button className="btn-pill btn-success" size="sm" onClick = {() => this.setState({addModalShow : true})}>
                    <strong><i class="icon-plus"></i> New Landlord</strong>
                  </Button> */}
                  <Button className="btn-pill btn-success btn-space" size="sm" onClick = {this.handleRefreshButton}>
                    <strong><i class="icon-refresh"></i> Refresh</strong>
                  </Button>
                  <form className="form-inline mr-auto formRight">
                      <input className="form-control mr-sm-2" type="text" placeholder="Search by Ticket" aria-label="Search" 
                      onChange={this.handleSearchInput}/>
                      {/* <button className="btn-pill btn-success" type="submit">Search</button> */}
                  </form>
              </div>
              <hr/>
              <div className="parentDiv">
              <Table hover responsive className="table-outline mb-0 d-none d-sm-table">
                <thead className="thead-light">
                <tr>
                  {/* <th className="text-center"><i className="icon-picture"></i></th> */}
                  {/* <th>Booking Ref</th> */}
                  <th>Landlord Ref</th>
                  <th>Slot Lable</th>
                  <th>Vechicle</th>
                  <th>Ticket</th>
                  <th>Hour(s)</th>
                  <th>Amount</th>
                  <th>Payment Status</th>
                  <th>Booking Status</th>
                  <th>Customer</th>
                  <th>Booked At</th>
                </tr>
                </thead>
                <tbody>
                {filteredItem.map((booking, index) => (
                <tr key={ index }>
                  {/* <td className="text-center">
                    <div className="avatar">
                      <img src={item.item_photo} className="img-avatar" alt="admin@bootstrapmaster.com" />
                      <span className="avatar-status badge-success"></span>
                    </div>
                  </td> */}
                  <td>
                    <div>{booking.landlord_code}</div>
                    <div className="small text-muted">
                      Booked: {booking.startTime} -- {booking.endTime}
                    </div>
                  </td>
                  <td className="">
                    <div>{booking.slot_name}</div>
                  </td>
                  <td className="">
                    <div>{booking.number_plate}</div>
                  </td>
                  <td className="">
                    <div>{booking.slot_ref}</div>
                  </td>
                  <td className="">
                    <div>{booking.totalTime}</div>
                  </td>
                  <td className="">
                    <div>{booking.payment}</div>
                  </td>
                  <td className="">
                    <div>{booking.payment_status}</div>
                  </td>
                  <td className="">
                    <div>{booking.parking_status}</div>
                  </td>
                  <td className="">
                    <div>{booking.customer_name}</div>
                  </td>
                  <td className="">
                    <div>{booking.updated_at}</div>
                  </td>
                  {/* <td>
                  <Label block color="info" size="sm" className="btn-pill bg-info" 
                    onClick={() => {}}><strong className="action">Details</strong>
                  </Label>
                  </td> */}
                </tr>
                ))}
                </tbody>
              </Table>
              {/* <AddLandlordModal
              show = {this.state.addModalShow}
              onHide = {addModalClose}/> */}

              {/* <EditModal 
                product_barcode = {productBarcode}
                product_name = {productName}
                manufactured_date = {manufacturedDate}
                expiry_date = {expiryDate}
                company_name = {companyName}
                city = {city}
                state = {state}
                province = {province}
                location = {location}
                postal_address = {postalAddress}
                plot_number = {plotNumber}
                contact = {contact}
                email = {email}
                website = {website}

                show= { this.state.editModalShow }
                onHide= { editModalClose }
              /> */}
          </div>
          </div>
      );
      }
    }
}

export default withRouter(BookingList);